<template>
  <div class="ranking">
    <h1>ランキング</h1>
    <el-table
      v-bind:data="tableData"
      stripe
      class="rankingTable">
      <el-table-column
        label="ランク"
        type="index"
        width="100px">
      </el-table-column>
      <el-table-column
        prop="name"
        label="しめい"
        width="180px">
      </el-table-column>
      <el-table-column
        prop="handleName"
        label="ハンドルネーム"
        width="180px">
      </el-table-column>
      <el-table-column
        prop="department"
        label="ぶしょ"
        width="150px">
      </el-table-column>
      <el-table-column
        prop="score"
        label="スコア"
        width="200px">
      </el-table-column>
    </el-table>
    <h2>and more...</h2>
  </div>
</template>

<script>
import axios from 'axios'
import router from '../router'

const httpClient = axios.create({
  baseURL: 'http://localhost:8080'
})

export default {
  name: 'ranking',
  data () {
    return {
      tableData: [],
      temporaryTableData: []
    }
  },
  mounted: function () {
    this.fetchRanking()
    for (let i = 0, l = this.temporaryTableData.length; i < l; i++) {
      if (this.temporaryTableData[i].displayNameEnable === false) {
        this.temporaryTableData[i].name = ''
      }
    }
    if (this.temporaryTableData.length >= 150) {
      this.tableData = this.temporaryTableData.slice(0, 149)
    }
  },
  methods: {
    fetchRanking: function () {
      httpClient.get('/player/ranking')
      .then((res) => {
        this.temporaryTableData = res.data
      }).catch((res) => {
        router.push({ path: 'Top' })
      })
    }
  }
}
</script>

<style>
  .rankingTable {
    width: min-content;
    margin: auto;
  }
</style>



